import React from 'react';
import Button from '@material-ui/core/Button';
import './Buttons.css';

function Buttons() {
    return (
        <div className="banner_buttons_div">
            <div className="button_1">
                <Button size="large"  variant="contained" color="default" href="https://drive.google.com/file/d/1tIp39pMM-jV0-T34vP6vCAGl9sR6QR18/view?usp=sharing">
                Learn More
                </Button>
            </div>
            <div className="button_2">
                <Button size="large"   variant="contained" color="primary" href="https://docs.google.com/document/d/1DEdGn6CSFp2USUzw35TK0F01a3TG33IfMT3TFS8Z8LE/edit?usp=sharing">
                Click Here for Stage-1 Results
                </Button>
            </div>
                        
        </div>
    )
}

export default Buttons
